import React from "react";
import { useMediaQuery } from "../misc/custom-hooks";

const Title = ({ title }) => {
  const { isMobile } = useMediaQuery("(max-width:992px)");
  return (
    <div full={isMobile}>
      <h1 className="text-center jskdf">{title}</h1>
    </div>
  );
};

export default Title;
