/* eslint-disable no-unused-vars */

import React, { useState } from "react";
import { Card } from "reactstrap";
// import Box from "@material-ui/core/Box";

import MainPageLayout from "../components/MainPageLayout";
import CategoryJsonArray from "../data/categories.json";
import ProductJsonArray from "../data/products.json";
// import Product from "./Product";
import { Link } from "react-router-dom";
import { useMediaQuery } from "../misc/custom-hooks";

const Category = () => {
  const { isMobile } = useMediaQuery("(max-width:992px)");
  const [categories] = useState(CategoryJsonArray);
  const [products] = useState(ProductJsonArray);

  return (
    <MainPageLayout>
      <Card className="mt-3 text-center ">
        {categories.map((item, index) => {
          return (
            <div full={isMobile} className="sc-jrsJWt bfXIoy">
              <a className="really" full={isMobile} href={item.id}>
                <span full={isMobile} className="setting">
                  <h2 full={isMobile}>{item.name}</h2>
                  <h5>{item.description}</h5>
                </span>
              </a>
            </div>
          );
        })}
      </Card>
      <div className="bfXIoy">
        <span className="text-center text-black cursor-pointer">
          <h2>Mouse</h2>
          <h5>Nice and smooth working</h5>
        </span>
      </div>
      <div className="bfXIoy">
        <span className="text-center text-black cursor-pointer">
          <h2>Speaker</h2>
          <h5>Portable speaker for the user experience</h5>
        </span>
      </div>
    </MainPageLayout>
  );
};

export default Category;
