import React from "react";
import "../styles/main.scss";

import { Link } from "react-router-dom";
import { Navbar as BootstrapNavbar, NavbarText } from "reactstrap";
// import { ShoppingCart } from "@material-ui/icons";
// import Checkout from "../pages/Checkout";
import { ReactComponent as CartSVG } from "../images/cart.svg";
import { useCart } from "../context/cart.context";
import { useMediaQuery } from "../misc/custom-hooks";

const LINKS = [
  { to: "/", text: "Home" },
  { to: "/category/:categoryId", text: "Category" },
  { to: "/product/:productId", text: "Product" },
];

const Navs = () => {
  const cart = useCart();
  const cartItemsTotal = cart.reduce((total, item) => total + item.quantity, 0);
  const cartPriceTotal = cart.reduce(
    (total, item) => total + item.price * item.quantity,
    0
  );
  const { isMobile } = useMediaQuery("(max-width:992px)");
  return (
    <BootstrapNavbar
      color="light"
      full={isMobile}
      light
      className="px-0 border-bottom mb-3"
    >
      <div full={isMobile}>
        <ul className="font-huge text-black" full={isMobile}>
          {LINKS.map((items) => (
            <li key={items.to}>
              <Link to={items.to}>{items.text}</Link>
            </li>
          ))}
          <NavbarText full={isMobile}>
            <Link
              to="/checkout"
              className="d-flex align-items-center"
              style={{ textDecoration: "none" }}
            >
              <CartSVG width={25} />
              <div
                className="circle bg-dark text-light rounded-circle d-flex justify-content-center align-items-center mx-2"
                style={{ width: 30, height: 30 }}
              >
                <span className="text-black">{cartItemsTotal}</span>
              </div>
              <div className="text-black">${cartPriceTotal}</div>
            </Link>
          </NavbarText>
        </ul>
      </div>
    </BootstrapNavbar>
  );
};

export default Navs;
